.wrapper {
  position: relative;
  z-index: 3;
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: rgb(20, 29, 38);
  padding-bottom: 100px;
  padding-left: 30px;
  padding-right: 30px;
}

.container {
  max-width: 700px;
  width: 100%;
  text-align: center;
}

.title {
  margin-bottom: 50px;
}

.myWorks {
  display: flex;
  flex-direction: column;
  gap: 200px;
}
